@font-face {
    font-family: ProximaNovaLight;
    font-weight: normal;
    src: url("../src/fontes/Proxima-Nova-Light.otf") format("opentype");
}

@font-face {
    font-family: ProximaNovaBold;
    font-weight: bold;
    src: url("../src/fontes/Proxima-Nova-Semibold.otf") format("opentype");
}


h1 {
    font-size: 2em;
}

h2 {
    font-size: 1.6em !important;
}

h3 {
    font-size: 1.4em;
}

@media (max-width: 1024px) {
    .btn-outline {}
}

.btn-outline {
    margin-top: 3%;
    border-radius: 0px;
    background: transparent;
    font-size: 20px;
    font-weight: 500;
    max-width: 100%;
    line-height: 1.7em !important;
    text-transform: uppercase !important;
    font-family: 'ProximaNovaLight', 'Montserrat', Helvetica, Arial, Lucida, sans-serif !important;
    padding: 0.3em 1em;
    margin-left: 2%;
}

.btn-outline span {
    flex-direction: column;
    flex-wrap: wrap;
}

.btn-white {
    border: 2px solid white;
    color: white;
}

.btn-white:hover {
    color: black;
    border: 2px solid white;
    background-color: white;
}

.btn-black {
    border: 2px solid black;
    color: black;
}

.btn-black:hover {
    color: white;
    border: 2px solid black;
    background-color: black;
}

.btn-red {
    border: 2px solid red;
    color: red;
}

.btn-red:hover {
    color: white;
    border: 2px solid red;
    background-color: red;
}

.carregando {
    background: url("http://i.stack.imgur.com/MnyxU.gif") center no-repeat;
    position: absolute;
    height: 100%;
    width: 100%
}

html {
    height: 100%;
}

body {
  
    font-family: 'ProximaNovaLight', 'Montserrat', sans-serif;
    font-size: 16px;
    color: white;
}

.body {
    height: 100vh;
    background: black;
    display: flex;
    align-items: center;
    justify-content: center;
}

.loader {
    position: relative;
    width: 75px;
    height: 100px;
}

.loader__bar {
    position: absolute;
    bottom: 0;
    width: 10px;
    height: 50%;
    background: white;
    transform-origin: center bottom;
    box-shadow: 1px 1px 0 rgba(0, 0, 0, 0.2);
}

.loader__bar:nth-child(1) {
    left: 0px;
    transform: scale(1, 0.2);
    animation: barUp1 4s infinite;
}

.loader__bar:nth-child(2) {
    left: 15px;
    transform: scale(1, 0.4);
    animation: barUp2 4s infinite;
}

.loader__bar:nth-child(3) {
    left: 30px;
    transform: scale(1, 0.6);
    animation: barUp3 4s infinite;
}

.loader__bar:nth-child(4) {
    left: 45px;
    transform: scale(1, 0.8);
    animation: barUp4 4s infinite;
}

.loader__bar:nth-child(5) {
    left: 60px;
    transform: scale(1, 1);
    animation: barUp5 4s infinite;
}

.loader__ball {
    position: absolute;
    bottom: 10px;
    left: 0;
    width: 10px;
    height: 10px;
    background: #fff;
    border-radius: 50%;
    animation: ball 4s infinite;
}

@keyframes ball {
    0% {
        transform: translate(0, 0);
    }

    5% {
        transform: translate(8px, -14px);
    }

    10% {
        transform: translate(15px, -10px);
    }

    17% {
        transform: translate(23px, -24px);
    }

    20% {
        transform: translate(30px, -20px);
    }

    27% {
        transform: translate(38px, -34px);
    }

    30% {
        transform: translate(45px, -30px);
    }

    37% {
        transform: translate(53px, -44px);
    }

    40% {
        transform: translate(60px, -40px);
    }

    50% {
        transform: translate(60px, 0);
    }

    57% {
        transform: translate(53px, -14px);
    }

    60% {
        transform: translate(45px, -10px);
    }

    67% {
        transform: translate(37px, -24px);
    }

    70% {
        transform: translate(30px, -20px);
    }

    77% {
        transform: translate(22px, -34px);
    }

    80% {
        transform: translate(15px, -30px);
    }

    87% {
        transform: translate(7px, -44px);
    }

    90% {
        transform: translate(0, -40px);
    }

    100% {
        transform: translate(0, 0);
    }
}

@keyframes barUp1 {
    0% {
        transform: scale(1, 0.2);
    }

    40% {
        transform: scale(1, 0.2);
    }

    50% {
        transform: scale(1, 1);
    }

    90% {
        transform: scale(1, 1);
    }

    100% {
        transform: scale(1, 0.2);
    }
}

@keyframes barUp2 {
    0% {
        transform: scale(1, 0.4);
    }

    40% {
        transform: scale(1, 0.4);
    }

    50% {
        transform: scale(1, 0.8);
    }

    90% {
        transform: scale(1, 0.8);
    }

    100% {
        transform: scale(1, 0.4);
    }
}

@keyframes barUp3 {
    0% {
        transform: scale(1, 0.6);
    }

    100% {
        transform: scale(1, 0.6);
    }
}

@keyframes barUp4 {
    0% {
        transform: scale(1, 0.8);
    }

    40% {
        transform: scale(1, 0.8);
    }

    50% {
        transform: scale(1, 0.4);
    }

    90% {
        transform: scale(1, 0.4);
    }

    100% {
        transform: scale(1, 0.8);
    }
}

@keyframes barUp5 {
    0% {
        transform: scale(1, 1);
    }

    40% {
        transform: scale(1, 1);
    }

    50% {
        transform: scale(1, 0.2);
    }

    90% {
        transform: scale(1, 0.2);
    }

    100% {
        transform: scale(1, 1);
    }
}